.header-bar h1 {
  color: white;
  margin: 10px auto;
  font-weight: 100;
  grid-column: 1;
  text-align: left;
}

.header-bar {
  display: grid;
  max-height: 6vh;
  grid-template-columns: 15% 55% 30%;
}

.dropdown {
  grid-column: 2;
  list-style-type: none;
  color: white;
}

.dropdown li {
  display: inline;
  cursor: pointer;
  padding: 22px 15px;
}

.dropdown li:hover {
  background: #241d6f;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  -webkit-box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  margin-top: 16px;
  z-index: 1000;
  color: black;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
.dropdown-content a:hover {
  background-color: #f1f1f1;
}

.dropdown li:hover .dropdown-content {
  display: block;
}

.header-buttons {
  grid-column: 3;
  list-style-type: none;
}

.header-buttons li {
  display: inline;
  padding: 22px 15px;
  cursor: pointer;
}

.header-buttons li a {
  color: white;
}

.header-buttons li:hover {
  background: #241d6f;
}

.search-bar,
select,
textarea {
  color: white;
}

.search-bar {
  grid-column: 2;
  margin: 5px;
  border: 2px solid white;
  background: transparent;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.search-bar:focus {
  outline: 0;
}

.search-bar::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: white;
}
.search-bar:-moz-placeholder,
textarea:-moz-placeholder {
  color: white;
}



