.systems {
  display: grid;
  grid-column: span 3;
  grid-template-rows: auto;
  grid-template-columns: auto;
  grid-gap: 10px;
  margin-top: 10px;
  height: 100%;
  width: 100%;
}

.system-card {
  width: 90%;
  border: 1px solid grey;
  align-self: center;
  justify-self: center;
  color: black;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  padding: 10px;
}

.system-card .system-header {
  background: #eee;
  grid-column: span 2;
  grid-row: 1;
  height: 45px;
}

.system-card .system-body {
  grid-row: 3;
  grid-column: span 2;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.system-card .system-body .charts .graphInfo button {
  font-size: 14px;
  height: 40px;
}

.system-card h3 {
  font-weight: 400;
  margin: 10px;
}

.system-card button {
  grid-column: span 2;
  grid-row: 5;
  width: 30%;
  height: 50px;
  font-size: 18px;
  background: #5c6ac4;
  border: 0;
  color: white;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  margin: 10px;
  cursor: pointer;
}

.system-card p {
  font-size: 20px;
  font-weight: 200;
  margin: 5px 10px;
}

.system-card .alarms {
  grid-row: 4;
  grid-column: span 2;
}

.system-card button:hover {
  background: #3e4eb2;
}

.system-card button:active {
  background: #1f32a9;
}

.system-card button:focus {
  outline: 0;
}

.system-card table {
  border: 1px solid #ccc;
  border-collapse: collapse;
  margin: 10px;
  padding: 0;
  width: 90%;
  table-layout: fixed;
}
.system-card table caption {
  font-size: 1.5em;
  margin: 0.5em 0 0.75em;
}
.system-card table tr {
  border: 1px solid #ddd;
  padding: 0.35em;
}
.system-card table th,
.system-card table td {
  padding: 0.625em;
  text-align: center;
}

.system-card table .status {
  max-width: 10px;
  padding: 0;
}

.system-card table .status.green {
  background: green;
}

.system-card table .status.yellow {
  background: yellow;
}

.system-card table .status.red {
  background: red;
}

.system-card table th {
  font-size: 0.85em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.system-card .system-focus {
  grid-row: 2;
  grid-column: span 2;
}

.system-card .system-focus table {
  border: 1px solid #ccc;
  border-collapse: collapse;
  padding: 0;
  width: 95%;
  table-layout: fixed;
}

.scoped-system {
  margin: 0;
  padding: 15px;
  display: grid;
  width: 95%;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  grid-column: span 3;
  grid-template-rows: auto;
  grid-template-columns: repeat(4, 1fr);
}

.scoped-system button {
  grid-row: 1;
  grid-column: 1;
  background: #5c6ac4;
  border: 0;
  color: white;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  width: 100%;
  height: 40px;
  margin-top: 10px;
  float: left;
  cursor: pointer;
}



