.plant-card {
  width: 90%;
  justify-self: center;
  border: 1px solid grey;
  min-height: 400px;
  max-height: 700px;
}

.plant-card h3 {
  font-size: 24px;
  font-weight: 500;
  margin: 0;
}

.plant-card .header {
  background: #eee;
  padding: 10px;
}

.plant-card .body {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  margin: 0;
  padding: 0;
}

.plant-card .body .info {
  grid-column-start: 2;
  grid-column-end: span 2;
  grid-row: 1;
  margin: 20px;
}

.plant-card .body .alarms {
  grid-column-start: 2;
  grid-column-end: span 2;
  -ms-flex-item-align: stretch;
      align-self: stretch;
  margin: 20px;
  grid-row: 2;
  width: 100%;
}

.plant-card .body button {
  grid-row: 3;
  grid-column: 1;
  margin: 10px;
  border: 0;
  padding: 10px;

  background: #5c6ac4;
  color: white;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  width: 100%;
  height: 40px;
  margin-top: 10px;
  cursor: pointer;
}


.plant-card .body button:hover {
  background: #3e4eb2;
}

.plant-card .body button:active {
  background: #1f32a9;
}

.plant-card .body button:focus {
  outline: 0;
}


