.assets {
  grid-row: 3;
  grid-column: span 4;
  margin: 0;
  padding: 0;
  width: 100%;
  display :grid;
  grid-template-columns: repeat(3,1fr);
  grid-template-rows: auto;
}

.timefilter {
  grid-row: 1;
  grid-column: span 3;
  background: #ECECEC;
}

.timefilter button {
  width: 100px;
  margin: 10px;
  font-size: 18px;
}

.timefilter h3 {
  margin: 10px;
  font-weight: 500;
  font-size: 18px;
}

.timefilter button:focus {
  outline: 0;
}

.timefilter button:hover {
  background: #3F4EAE;
}


.timefilter button:active {
  background: #2032A2;
}


