.settings-area .menu li {
  list-style-type: none;
  cursor: pointer;
  padding: 15px;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.settings-area .menu .active {
  background-color: #686680;
  color: white;
}

.settings-area .menu li:hover {
  background-color: #4E4C67;
  color: white;
  padding: 20px;
}

.settings-area .menu ul {
  display: inline;
  margin: 0;
  padding: 0;
}

.menu {
  height: 100%;
  grid-column: 1;
  border-right: 1px solid #86BBD8;
}


