.settings-area {
  height: 70%;
  width: 90%;
  background: white;

  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;

  max-width: 100%;
  max-height: 100%;
  overflow: auto;

  display: grid;
  grid-template-columns: 15% 85%;
  grid-template-rows: auto;
}

.settings-area h3 {
  color: black;
  font-weight: 500;
}



