.menu-content {
  height: 100%;
  grid-column: 2;
}

.menu-content table {
  text-decoration: none;
  font-size: 16px;
  border: 1px solid fafafa;
  border-radius: 10px;
  width: 100%;
}

.menu-content table th {
  padding: 21px 25px 22px 25px;
  background: #ededed;
  border-top: 1px solid #fafafa;
  border-bottom: 1px solid #e0e0e0;
  font-weight: 500;
  font-size: 20px;
  text-align: left;
}

.menu-content table tr {
  text-align: center;
  padding-left: 20px;
}

.menu-content table td:first-child {
  text-align: left;
  padding-left: 20px;
  border-left: 0;
}

.menu-content table td {
  padding: 18px;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #e0e0e0;
  border-left: 1px solid #e0e0e0;

  background: #fafafa;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#fbfbfb),
    to(#fafafa)
  );
  background: -moz-linear-gradient(top, #fbfbfb, #fafafa);
}

.menu-content table tr.even td {
  background: #f6f6f6;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#f8f8f8),
    to(#f6f6f6)
  );
  background: -moz-linear-gradient(top, #f8f8f8, #f6f6f6);
}

.menu-content table tr:last-child td {
  border-bottom: 0;
}

.menu-content table tr:last-child td:first-child {
  border-bottom-left-radius: 3px;
}
.menu-content table tr:last-child td:last-child {
  border-bottom-right-radius: 3px;
}

.menu-content table tr:hover td {
  background: #f2f2f2;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#f2f2f2),
    to(#f0f0f0)
  );
  background: -moz-linear-gradient(top, #f2f2f2, #f0f0f0);
}

.menu-content input[type="text"],
select {
  margin-bottom: 10px;
  padding: 10px;
  width: 100%;
  border: 1px solid #ccc;
}

.menu-content input[type="number"],
select {
  margin-bottom: 10px;
  padding: 10px;
  width: 100%;
  border: 1px solid #ccc;
}

.menu-content input[type="submit"] {
  width: 50%;
  background-color: #4caf50;
  color: white;
  padding: 10px;
  border: none;
  cursor: pointer;
}

.menu-content input[type="submit"]:hover {
  background-color: #45a049;
}

.menu-content form {
  padding: 20px;
  width: 40%;
}

.menu-content select {
  color: black;
}

.menu-content h4 {
  grid-column: 1;
}

.triangle {
  border-right: 5px solid; 
  border-bottom: 5px solid;
  height: 10px;
  width: 10px;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
  grid-column: 2;
  justify-self: center;
  -ms-flex-item-align: center;
      align-self: center;
}

.header-controls {
  display: grid;
  width: 50%;
  margin: 5px 0;
  grid-template-columns: repeat(2,1fr);
}



