.alarms table {
  border: 1px solid #ccc;
  border-collapse: collapse;
  margin: 10px;
  padding: 0;
  width: 90%;
  table-layout: fixed;
}
.alarms table caption {
  font-size: 1.5em;
  margin: 0.5em 0 0.75em;
}
.alarms table tr {
  border: 1px solid #ddd;
  padding: 0.35em;
}
.alarms table th,
.alarms table td {
  padding: 0.625em;
  text-align: center;
}
.alarms table th {
  font-size: 0.85em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}


