.charts {
  display: grid;
  grid-template-rows: auto;
}

.graphInfo {
  grid-row: 1;
  width: 100%;
}

.graphInfo button {
  width: 35%;
  margin: 5px;
  text-align: center;
  background-size: 200% auto;
  border: 0 solid white;
  text-transform: uppercase;
  color: white;
  border-radius: 20px;
  background: #a1c5fd;
}

.graph {
  grid-row: 2;
}

.graphInfo button:focus {
  outline: 0;
}

.graphInfo button:hover {
  background: #78aaf9;
}

.graphInfo button:active {
  background: #094c5f;
}



